import React, { Component } from "react";
import { Link } from "react-router-dom";
import _ from "lodash";
import familyData from "../Tree/khandelwal-data";

import "./Profile.css";

class MyTree extends Component {
  constructor(props) {
    super(props);
    this.state = {
      profileId: this.props.profileId,
      spouseId: this.props.spouseId,
      familyData: this.find(
        parseInt(this.props.profileId),
        familyData.DATA_SET2
      ),
      spouseData: this.props.spouseId
        ? this.getSpouseData(parseInt(this.props.spouseId))
        : null
    };
  }

  componentDidMount = () => {};

  find(id, items) {
    var i = 0,
      found;
    for (; i < items.length; i++) {
      if (items[i].id === id) {
        return items[i];
      } else if (_.isArray(items[i].children)) {
        found = this.find(id, items[i].children);
        if (found) {
          return found;
        }
      }
    }
  }

  renderMySpouse() {
    var arr = [];
    let data = this.state.familyData;
    if (data)
      _.forEach(data.spouse, function(value, key) {
        arr.push(
          <Link
            title={value.name}
            to={{
              pathname: "/profile-detail/",
              search: "?id=" + data.id
            }}
            className="spouse"
            key={key}
            replace
          >
            {value.name}
          </Link>
        );
      });
    return arr;
  }

  renderChildren() {
    var arr = [];
    let data = this.state.familyData;
    if (data)
      _.forEach(data.children, function(value, key) {
        arr.push(
          <Link
            title={value.name}
            to={{
              pathname: "/profile-detail/",
              search: "?id=" + value.id
            }}
            className="spouse"
            key={key}
            replace
          >
            {value.name}
          </Link>
        );
      });
    return arr;
  }

  calcAge(dateString) {
    var birthday = +new Date(dateString);
    return ~~((Date.now() - birthday) / 31557600000);
  }
  getSpouseData(sId) {
    let data = this.find(parseInt(this.props.profileId), familyData.DATA_SET2);
    let spouseData = _.find(data.spouse, function(o) {
      if (o.id === sId) return o;
    });
    return spouseData;
  }

  render() {
    console.log("this.state ", this.state);
    return (
      <div className="profile-detail-wrapper">
        <div className="profile-detail">
          {this.state.spouseId && this.state.spouseData && (
            <div className="profile-inner">
              <nav className="navbar profile-bar">
                <span className="navbar-brand mb-0 h1">
                  {this.state.spouseData.name}
                </span>
                <Link
                  title="go back"
                  to="/"
                  className="btn btn-light float-right"
                >
                  Back to list
                </Link>
              </nav>
              <div className="row no-gutters">
                <div className="col-md-4">
                  <div className="image-block-container">
                    {this.state.spouseData.imagePath && (
                      <span
                        className={
                          `image-block m-auto img_` +
                          this.state.spouseData.imagePath
                        }
                      />
                    )}
                  </div>
                </div>
                <div className="col-md-8">
                  <div className="card-body">
                    <h5 className="card-title">{this.state.spouseData.name}</h5>
                    {this.state.familyData && this.state.familyData.birthdate && (
                      <p>
                        Born: {this.state.spouseData.birthdate} (age{" "}
                        {this.calcAge(this.state.spouseData.birthdate)})
                      </p>
                    )}
                    {this.state.spouseData &&
                      this.state.spouseData.alive !== undefined &&
                      this.state.spouseData.alive === false && <p>Died</p>}
                    <p>
                      Spouse(s):{" "}
                      <Link
                        title={this.state.familyData.name}
                        to={{
                          pathname: "/profile-detail/",
                          search: "?id=" + this.state.familyData.id
                        }}
                        className="spouse"
                        replace
                      >
                        {this.state.familyData.name}
                      </Link>
                    </p>
                    <p>Children: {this.renderChildren()}</p>
                    <p>Location: {this.state.spouseData.location} </p>
                  </div>
                </div>
              </div>
            </div>
          )}
          {!this.state.spouseId && !this.state.spouseData && (
            <div className="profile-inner">
              <nav className="navbar profile-bar">
                {!this.state.spouseId && (
                  <span className="navbar-brand mb-0 h1">
                    {this.state.familyData && this.state.familyData.name}
                  </span>
                )}
                {this.state.spouseId && this.state.spouseData && (
                  <span className="navbar-brand mb-0 h1">
                    {this.state.spouseData.name}
                  </span>
                )}
                <Link
                  title="go back"
                  to="/"
                  className="btn btn-light float-right"
                >
                  Back to list
                </Link>
              </nav>
              <div className="row no-gutters">
                <div className="col-md-4">
                  <div className="image-block-container">
                    {this.state.familyData && (
                      <span
                        className={
                          `image-block m-auto img_` +
                          this.state.familyData.imagePath
                        }
                      />
                    )}
                  </div>
                </div>
                <div className="col-md-8">
                  <div className="card-body">
                    {!this.state.spouseId && (
                      <h5 className="card-title">
                        {this.state.familyData && this.state.familyData.name}
                      </h5>
                    )}
                    {this.state.spouseId && this.state.spouseData && (
                      <h5 className="card-title">
                        {this.state.spouseData.name}
                      </h5>
                    )}

                    {this.state.familyData && this.state.familyData.birthdate && (
                      <p>
                        Born: {this.state.familyData.birthdate} (age{" "}
                        {this.calcAge(this.state.familyData.birthdate)})
                      </p>
                    )}
                    {this.state.familyData &&
                      this.state.familyData.alive !== undefined &&
                      this.state.familyData.alive === false && <p>Died</p>}
                    <p>Spouse(s): {this.renderMySpouse()}</p>
                    <p>Children: {this.renderChildren()}</p>
                    <p>
                      Location:{" "}
                      {this.state.familyData && this.state.familyData.location}{" "}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}
export default MyTree;
